const USMap = () => (
 <div>
  <ComposableMap projection="geoAlbersUsa">
   <Geographies geography={geoUrl}>
    {({ geographies }) =>
     geographies.map((geo) => (
      <Geography
       key={geo.rsmKey}
       geography={geo}
       fill="#EAEAEC"
       stroke="#D6D6DA"
      />
     ))
    }
   </Geographies>
   {locations.map(({ name, coordinates }, index) => (
    <Marker key={index} coordinates={coordinates}>
     <circle
      r={6}
      fill="#ee732f"
      data-tooltip-id="map-tooltip"
      data-tooltip-content={name}
     >
      <animate
       attributeName="r"
       from="6"
       to="6"
       dur="1s"
       repeatCount="indefinite"
       begin="0s"
       fill="freeze"
      />
     </circle>
    </Marker>
   ))}
  </ComposableMap>
  <ReactTooltip id="map-tooltip" />
 </div>
);

export default USMap;
